import { defineStore } from "pinia";
import { useDrugStore } from "./store.drugs";
import { usePatientsStore } from "./store.patients";

const drugStore = useDrugStore()
const patientsStore = usePatientsStore()
export const useCalculationStore = defineStore("CalculationStore", {
    state: () => ({
        howToUseInfo: "",
        inputCalParams: {
            dose: 0,
            ampule: 0,
            dilution: 10,
            duration: 1,
            infusionRate: 0,
            unitConverted: {
                dose: 0,
                concentration: 0,
                ampuleUnit: 0, // Chuyển đơn vị mg/ gram về mcg để tính toán
            },
            toCalWhat: {
                // Nhập Ống thuốc -> tính Liều mcg/kg
                from_Ampule_to_Dose: false,
                input: "input_dose", // cal ampule; input_ampule = cal dose
            }
        }
    }),
    actions: {
        // todo: tính liều bolus / *chích nhát*
        // @param: changeWcalW: string = 
        // 1. changeWcalW = input_dose -> changed dose calculate ampule
        // 2. changeWcalW = input_ampule -> changed ampule calculate dose   
        calBolusDoseChanged(changeWcalW?: string) {
            // console.log("this.inputCalParams.toCalWhat.input", this.inputCalParams.toCalWhat.input, "changeWcalW", changeWcalW)
            //stop condition
            if (this.inputCalParams.toCalWhat.input != changeWcalW && changeWcalW != undefined) {
                console.log("calBolusDoseChanged", "stop calculate")
                return false;
            }
            //Nếu cấp param cal (tức dose, hoặc ampule thay đổi) thì set tính, còn không thì giữ nguyên như cũ (tức mình thay đổi dilution hoặc duration)
            if (this.inputCalParams.toCalWhat.input == "input_dose") {
                this._convertCorrectedUnitConcentration()
                this._convertCorrectedUnitDose()
                // Tính số ampule cần sử dụng để được liều mong muốn
                this.inputCalParams.ampule = this.inputCalParams.unitConverted.dose * patientsStore.currentPatient.weight! / this.inputCalParams.unitConverted.ampuleUnit
            } else if (this.inputCalParams.toCalWhat.input == "input_ampule") {
                this._convertCorrectedUnitConcentration()
                this.inputCalParams.dose = this.inputCalParams.unitConverted.ampuleUnit * this.inputCalParams.ampule / patientsStore.currentPatient.weight!
                // this._convertCorrectedUnitDose()
                this._convertToShowDose(this.inputCalParams.dose)
            }
            // Tính tốc độ truyền
            this.inputCalParams.infusionRate = this.inputCalParams.dilution / this.inputCalParams.duration

            // Cập nhật thông tin sử dụng thuốc
            // console.log(this.inputCalParams)
            this.howToUseInfo = `
            Dilute <b>${this.inputCalParams.ampule} ${drugStore.currentDrug.DOSE_FORM}</b> <b></i>(${this.inputCalParams.ampule * drugStore.currentDrug.SOLVENT!} mL)</i></b> of <b>${drugStore.currentDrug.GENERIC_NAME}</b> up to <b>${this.inputCalParams.dilution} mL</b> of dilution.<br/>

            Dose is: <b>${this.inputCalParams.dose} ${drugStore.currentDrug.DOSE_UNIT}/${drugStore.currentDrug.PER_TIME}</b> <br/>
            Infusion rate is: <b>${this.inputCalParams.infusionRate} mL/hour</b> <br/>
            Final concentration: ${this.inputCalParams.unitConverted.concentration} mcg/mL.<br/>
              Estimation time is: ${this.inputCalParams.dilution / this.inputCalParams.infusionRate} hours.<br/>`

        },
        //@return: liều thuốc đổi về mcg/kg  /min
        _convertCorrectedUnitDose() {
            // DOSE mcg/kg
            let correctedDose = this.inputCalParams.dose
            if (drugStore.currentDrug.DOSE_UNIT == "mg/kg") {
                correctedDose = correctedDose * 1000 // -> mcg/kg
            } else if (drugStore.currentDrug.DOSE_UNIT == "gram/kg") {
                correctedDose = correctedDose * 1000000 // -> mcg/kg
            }
            // Dose (-> mcg/kg    /min), chuyển trên Giờ thành trên Phút
            if (drugStore.currentDrug.PER_TIME == "hour") {
                correctedDose = correctedDose / 60
            }
            // console.log("correctedDose: " + correctedDose)
            this.inputCalParams.unitConverted.dose = correctedDose // mcg/kg/min
        },
        //@return mcg drug/ ml: số mcg trên mỗi mililit
        _convertCorrectedUnitConcentration() {
            // Nồng độ = -> mcg/mL
            // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
            let correctedConcentrat = drugStore.currentDrug.AMOUNT! * this.inputCalParams.ampule / this.inputCalParams.dilution
            if (drugStore.currentDrug.AMOUNT_UNIT == "mg") {
                correctedConcentrat = correctedConcentrat * 1000
                this.inputCalParams.unitConverted.ampuleUnit = drugStore.currentDrug.AMOUNT! * 1000

            } else if (drugStore.currentDrug.AMOUNT_UNIT == "gram") {
                correctedConcentrat = correctedConcentrat * 1000000

                this.inputCalParams.unitConverted.ampuleUnit = drugStore.currentDrug.AMOUNT! * 1000000
            }
            // console.info("convertCorrectedUnitConcentration ", correctedConcentrat)
            this.inputCalParams.unitConverted.concentration = correctedConcentrat
        },
        //@return trả về đơn vị ban đầu
        _convertToShowDose(calculatedDose) {
            let correctedDose = calculatedDose
            if (drugStore.currentDrug.DOSE_UNIT == "mg/kg") {
                correctedDose = correctedDose / 1000 // -> mcg/kg
            } else if (drugStore.currentDrug.DOSE_UNIT == "gram/kg") {
                correctedDose = correctedDose / 1000000 // -> mcg/kg
            }
            if (drugStore.currentDrug.PER_TIME == "hour") {
                correctedDose = correctedDose * 60
            }
            this.inputCalParams.dose = correctedDose
        },
        __truyenLienTucCalWhat(cal: string) {
            if (cal == 'input_dose' && !this.inputCalParams.toCalWhat.from_Ampule_to_Dose) {
                this.inputCalParams.toCalWhat.input = "input_ampule"

            }
            else if (cal == 'input_ampule' && this.inputCalParams.toCalWhat.from_Ampule_to_Dose) {
                this.inputCalParams.toCalWhat.input = "input_dose"
            }
            // console.log("__truyenLienTucCalWhat", "cal",cal, "set input", this.inputCalParams.toCalWhat.input)

            // console.log("__truyenLienTucCalWhat stop2", cal)

            if (cal == "input_dose") {
                this.inputCalParams.toCalWhat.from_Ampule_to_Dose = true
            } else {
                this.inputCalParams.toCalWhat.from_Ampule_to_Dose = false
            }
        }
    }
})