
import { defineComponent } from 'vue';
import { calculatorOutline } from "ionicons/icons";
import {
  IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonButtons, IonBackButton, IonButton,
  IonCard, IonCardContent, IonCardHeader, IonCardSubtitle, IonCardTitle,
  IonCol, IonGrid, IonRow,
  IonInput, IonItem, IonLabel,
  // IonButton,IonIcon,
  // IonToggle,
  IonSegment, IonSegmentButton,
  IonModal
} from '@ionic/vue';
import { useDrugStore } from "@/stores/store.drugs";

import nerdamer from "nerdamer/all";

import { usePatientsStore } from '@/stores/store.patients';
import { useCalculationStore } from '@/stores/store.calculations';
export default defineComponent({
  name: 'drugsCalculateDose',
  components: {
    IonHeader, IonToolbar, IonTitle, IonContent, IonPage, IonButtons, IonBackButton, IonButton,
    IonCard, IonCardContent, IonCardHeader, IonCardSubtitle, IonCardTitle,
    IonCol, IonGrid, IonRow,
    IonInput, IonItem, IonLabel,
    // IonButton, IonIcon,
    // IonToggle,
    IonSegment, IonSegmentButton,
    IonModal
  },
  setup() {
    const drugStore = useDrugStore()
    // Sử dụng store patients và cài đặt giá trị ban đầu cho nó
    const patientStore = usePatientsStore()
    const calculationStore = useCalculationStore()
    patientStore.currentPatient.weight = patientStore.currentPatient.weight ? patientStore.currentPatient.weight : 50
    // Cài curent Drug ban đầu
    return {
      drugStore, patientStore, calculationStore,
      calculatorOutline
    }
  },
  data() {
    return {
      calAmpule: 1,
      calDilution: 25,
      calDose: 0,
      calInfusionRate: 0,
      calDuration: 0,
      toCalWhat: "toCalInfusionRate", // dành cho truyền liên tục
      unitConverted: {
        dose: 0,
        concentration: 0
      },
      information: ''
    }
  },
  methods: {
    
    convertCorrectedUnitDose() {
      // DOSE mcg/kg
      let correctedDose = this.calDose
      if (this.drugStore.currentDrug.DOSE_UNIT == "mg/kg") {
        correctedDose = correctedDose * 1000 // -> mcg/kg
      } else if (this.drugStore.currentDrug.DOSE_UNIT == "gram/kg") {
        correctedDose = correctedDose * 1000000 // -> mcg/kg
      }
      // Dose (-> mcg/kg    /min), chuyển trên Giờ thành trên Phút
      if (this.drugStore.currentDrug.PER_TIME == "hour") {
        correctedDose = correctedDose / 60
      }
      // console.log("correctedDose: " + correctedDose)
      this.unitConverted.dose = correctedDose // mcg/kg/min
    },
    convertCorrectedUnitConcentration() {
      // Nồng độ = -> mcg/mL
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      let correctedConcentrat = this.drugStore.currentDrug.AMOUNT! * this.calAmpule / this.calDilution
      if (this.drugStore.currentDrug.AMOUNT_UNIT == "mg") {
        correctedConcentrat = correctedConcentrat * 1000
      } else if (this.drugStore.currentDrug.AMOUNT_UNIT == "gram") {
        correctedConcentrat = correctedConcentrat * 1000000
      }
      // console.info("convertCorrectedUnitConcentration ", correctedConcentrat)
      this.unitConverted.concentration = correctedConcentrat
    },
    convertToShowDose(calculatedDose) {
      let correctedDose = calculatedDose
      if (this.drugStore.currentDrug.DOSE_UNIT == "mg/kg") {
        correctedDose = correctedDose / 1000 // -> mcg/kg
      } else if (this.drugStore.currentDrug.DOSE_UNIT == "gram/kg") {
        correctedDose = correctedDose / 1000000 // -> mcg/kg
      }
      if (this.drugStore.currentDrug.PER_TIME == "hour") {
        correctedDose = correctedDose * 60
      }
      this.calDose = correctedDose
    },
    changeToCalWhat() {
      this.information = ''
      if (this.toCalWhat != "toCalMgInAmpules") {
        this.calDuration = 0
      }
      this.calculateDrug_TruyenLienTuc('')

    },
    calculateDrug_TruyenLienTuc(whoChanged) {
      if (this.toCalWhat == "toCalInfusionRate") {
        if (whoChanged == "infusionRate") {
          return false;
        }
        /* 
        Tính toCalInfusionRate nếu nó là single hoặc dose hoặc day
        */
        if (this.drugStore.currentDrug.PER_TIME == "single" || this.drugStore.currentDrug.PER_TIME == "dose") {
          // Hiển thị thời gian truyền
          this.calDuration == 0 ? this.calDuration = 1 : false
          // Ẩn luôn Ampules
          this.convertCorrectedUnitConcentration()
          this.calInfusionRate = this.calDilution / this.calDuration
          // Tính số mg/ml phải lấy

          this.information = `
          To get the dose <b>${this.calDose} ${this.drugStore.currentDrug.DOSE_UNIT}/${this.drugStore.currentDrug.PER_TIME}</b>
          <br/>
          You get <b>${this.calDose * this.patientStore.currentPatient.weight!}mg (= ${this.drugStore.currentDrug.SOLVENT! * this.calDose * this.patientStore.currentPatient.weight! / this.drugStore.currentDrug.AMOUNT!}ml) of ${this.drugStore.currentDrug.GENERIC_NAME}</b> and diluted up to ${this.calDilution}ml of dilution.
          <br/>
          Infusion rate is: <b>${this.calInfusionRate} mL/hour</b>
          <br/>
          Frequency: each ${this.drugStore.currentDrug.FREQ} hours (= x${24 / Number(this.drugStore.currentDrug.FREQ)} time each day)
          <br/>
          Estimation time is: ${this.calDilution / this.calInfusionRate} hours.<br/>

          `

        } else if (this.drugStore.currentDrug.PER_TIME == "day") {
          this.calDuration = 1
        }
        /* Nếu không phải infusionRate thì tính infusionRate */
        // if (this.currentDrug.PER_TIME == "min" || this.currentDrug.PER_TIME == "hour") {
        //   /*
        //     Infusion rate (mL/h) = Dose (mcg/kg/min) * Weight (kg) / Concentration (mcg/mL) * 60
        //     r = d * w / c * 60
        //   */
        // }

        this.convertCorrectedUnitDose()
        this.convertCorrectedUnitConcentration()
        if (this.drugStore.currentDrug.PER_TIME == "min" || this.drugStore.currentDrug.PER_TIME == "hour") {
          this.calInfusionRate = nerdamer("r = d * w / c * 60", { d: this.unitConverted.dose, w: this.patientStore.currentPatient.weight, c: this.unitConverted.concentration }).solveFor("r").toDecimal()

          this.information = `Dilute <b>${this.calAmpule} ${this.drugStore.currentDrug.DOSE_FORM}</b> of <b>${this.drugStore.currentDrug.GENERIC_NAME}</b> up to <b>${this.calDilution} mL</b> of dilution.<br/>
          Final concentration: ${this.unitConverted.concentration} mcg/mL.<br/>
          Infusion rate is: <b>${this.calInfusionRate} mL/hour</b> <br/>
          Dose is: <b>${this.calDose} ${this.drugStore.currentDrug.DOSE_UNIT}/${this.drugStore.currentDrug.PER_TIME}</b> <br/>
          Estimation time is: ${this.calDilution / this.calInfusionRate} hours.<br/>
          `
        } else if (this.drugStore.currentDrug.PER_TIME == "single" || this.drugStore.currentDrug.PER_TIME == "dose") {
          this.calInfusionRate = this.calDilution / this.calDuration
          console.log("A")
        }
      }
      else if (this.toCalWhat == "toCalDose") {
        /* Nếu  infusionRate thì tính infusionRate: calDose hoặc số mg ml trong ampulse
        1. Hiện cái lock khóa cho 2 cái calDose và calAmpulse
        2. Khóa thằng nào thì tính thằng còn lại
        */
        // Dose (-> mcg/kg    /min)
        this.convertCorrectedUnitConcentration()
        let cal = nerdamer("r = d * w / c * 60", { r: this.calInfusionRate, w: this.patientStore.currentPatient.weight, c: this.unitConverted.concentration }).solveFor("d")
        this.convertToShowDose(nerdamer(cal.toString()).toDecimal())

        this.information = `Dilute <b>${this.calAmpule} ${this.drugStore.currentDrug.DOSE_FORM}</b> of <b>${this.drugStore.currentDrug.GENERIC_NAME}</b> up to <b>${this.calDilution} mL</b> of dilution.<br/>
          Final concentration: ${this.unitConverted.concentration} mcg/mL.<br/>
          Infusion rate is: <b>${this.calInfusionRate} mL/hour</b> <br/>
          Dose is: <b>${this.calDose} ${this.drugStore.currentDrug.DOSE_UNIT}/${this.drugStore.currentDrug.PER_TIME}</b> <br/>
          Estimation time is: ${this.calDilution / this.calInfusionRate} hours.<br/>`
      }
      else if (this.toCalWhat == "toCalMgInAmpules") {
        /* Nếu  infusionRate thì tính infusionRate: calDose hoặc số mg ml trong ampulse
        1. Tính số mg trong ống theo liều định trước (dose, single)
        */
        this.calDuration = 25

        this.convertCorrectedUnitConcentration()
        let cal = nerdamer("r = d * w / c * 60", { r: this.calInfusionRate, w: this.patientStore.currentPatient.weight, c: this.unitConverted.concentration }).solveFor("d")
        this.convertToShowDose(nerdamer(cal.toString()).toDecimal())
        // console.log(nerdamer("r = d * w / c * 60", { r: this.calInfusionRate, w: this.weight, c: this.unitConverted.concentration }).solveFor("d").toString())

        this.information = `Dilute <b>${this.calAmpule} ${this.drugStore.currentDrug.DOSE_FORM}</b> of <b>${this.drugStore.currentDrug.GENERIC_NAME}</b> up to <b>${this.calDilution} mL</b> of dilution.<br/>
        Dose is: <b>${this.calDose} ${this.drugStore.currentDrug.DOSE_UNIT}/${this.drugStore.currentDrug.PER_TIME}</b> <br/>
        Infusion rate is: <b>${this.calInfusionRate} mL/hour</b> <br/>
        Final concentration: ${this.unitConverted.concentration} mcg/mL.<br/>
        Estimation time is: ${this.calDilution / this.calInfusionRate} hours.<br/>`
      }
    }
  },
  created() {
    try {
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      // this.calculateDrug("")
      //TODO: tạo lại Thuốc hiện tại trong store
      this.drugStore.updateCurrentDrug(Number(this.$route.params.id))
      // TODO: chỉnh liều tối thiểu cho nó
      this.calDose = this.drugStore.currentDrug.MIN_DOSE! | 0
      this.calculationStore.inputCalParams.dose = this.drugStore.currentDrug.MIN_DOSE! | 0
      // Nếu là = CHÍCH NHÁT, thì cài đặt ban đầu lại
      if (this.drugStore.currentDrug.cal_typeCalculate != 'truyenLienTuc') {
        this.calDuration = 0
        this.calDilution = 10
        this.calculationStore.calBolusDoseChanged()
      }
      // Chích nhát
      else {
        return false;
      }

    } catch (error) {
      this.$router.push({
        name: "drugs"
      })
    }
  },
});
